import React from 'react'
import {
  Card,
  CardBody,
  Row,
  Col,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane
} from 'reactstrap'
import classnames from 'classnames'
import { User, Info, Share, Bell, Mail } from 'react-feather'
import AccountTab from './Account'
import InfoTab from './Information'
import PaymentTab from './Payments'
import Notifications from './Notifications'
import ContactUs from './ContactUs'
import '../../../assets/scss/pages/users.scss'

class UserEdit extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      activeTab: '1',
      path: props.location.pathname
    }
  }
  toggle = (tab) => {
    this.setState({
      activeTab: tab
    })
  }

  componentDidMount() {
    const { path } = this.state
    if (path === '/user/Upgrade') {
      this.setState({ activeTab: '4' })
    } else if (path === '/user/feed') {
      this.setState({ activeTab: '5' })
    } else if (path === '/user/notify') {
      this.setState({ activeTab: '6' })
    } else if (path === '/user/profile') {
      this.setState({ activeTab: '2' })
    }
  }
  render() {
    console.log('props', this.props)
    return (
      <Card style={{ maxHeight: 'calc(100vh - 80px)', marginBottom: '0px' }}>
        <CardBody className="pt-2">
          <Row>
            <Col sm="12" className="d-flex justify-content-start flex-wrap">
              <CardBody style={{ padding: '0px' }}>
                <Nav tabs>
                  {/* <NavItem className="mr-5">
                      <NavLink
                        className={classnames({
                          active: this.state.activeTab === '1'
                        })}
                        onClick={() => {
                          this.toggle('1')
                        }}
                      >
                        <User size={16} />
                        <span className="align-middle ml-50">Account</span>
                      </NavLink>
                    </NavItem> */}
                  <NavItem className="mr-5">
                    <NavLink
                      className={classnames({
                        active: this.state.activeTab === '2'
                      })}
                      onClick={() => {
                        this.toggle('2')
                      }}
                    >
                      <Info size={16} />
                      <span className="align-middle ml-50">Information</span>
                    </NavLink>
                  </NavItem>
                  <NavItem className="mr-5">
                    <NavLink
                      className={classnames({
                        active: this.state.activeTab === '4'
                      })}
                      onClick={() => {
                        this.toggle('4')
                      }}
                    >
                      <Share size={16} />
                      <span className="align-middle ml-50">Subscription</span>
                    </NavLink>
                  </NavItem>

                  <NavItem className="mr-5">
                    <NavLink
                      className={classnames({
                        active: this.state.activeTab === '5'
                      })}
                      onClick={() => {
                        this.toggle('5')
                      }}
                    >
                      <Mail size={16} />
                      <span className="align-middle ml-50">Contact Us</span>
                    </NavLink>
                  </NavItem>
                  <NavItem className="mr-5">
                    <NavLink
                      className={classnames({
                        active: this.state.activeTab === '6'
                      })}
                      onClick={() => {
                        this.toggle('6')
                      }}
                    >
                      <Bell size={16} />
                      <span className="align-middle ml-50">Notifications</span>
                    </NavLink>
                  </NavItem>
                </Nav>
                <TabContent activeTab={this.state.activeTab}>
                  {/* <TabPane tabId="1">
                      <AccountTab />
                    </TabPane> */}
                  <TabPane
                    className="card_body"
                    tabId="2"
                    style={{
                      maxHeight: 'calc(100vh - 160px)',
                      minHeight: 'calc(100vh - 160px)',
                      overflowY: 'overlay'
                    }}
                  >
                    <InfoTab />
                  </TabPane>
                  <TabPane
                    className="card_body"
                    tabId="4"
                    style={{
                      maxHeight: 'calc(100vh - 160px)',
                      minHeight: 'calc(100vh - 160px)',
                      overflowY: 'overlay'
                    }}
                  >
                    <PaymentTab />
                  </TabPane>
                  <TabPane
                    className="card_body"
                    tabId="5"
                    style={{
                      maxHeight: 'calc(100vh - 160px)',
                      minHeight: 'calc(100vh - 160px)',
                      overflowY: 'overlay'
                    }}
                  >
                    <ContactUs />
                  </TabPane>
                  <TabPane
                    className="card_body"
                    tabId="6"
                    style={{
                      maxHeight: 'calc(100vh - 160px)',
                      minHeight: 'calc(100vh - 160px)',
                      overflowY: 'overlay'
                    }}
                  >
                    <Notifications />
                  </TabPane>
                  {/* <TabPane tabId="3">
                      <SocialTab />
                    </TabPane> */}
                </TabContent>
              </CardBody>
            </Col>
          </Row>
        </CardBody>
      </Card>
    )
  }
}
export default UserEdit
