import React from 'react'
import { Media, Row, Col, Button, Input, Label, Container } from 'reactstrap'
import axios from 'axios'
import avatar1 from '../../../assets/img/avatar/female.png'
import avatar2 from '../../../assets/img/avatar/male.png'
import Select from 'react-select'
import '../../../assets/scss/plugins/extensions/dropzone.scss'

const contactUsOptions = [
  {
    value: 'Query',
    label: 'Query',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Complain',
    label: 'Complain',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Feedback',
    label: 'Feedback',
    color: '#00B8D9',
    isFixed: true
  }
]

class UserAccountTab extends React.Component {
  state = {
    saveButtonText: 'Save',
    contactUsFormType: '',
    contactUsDescription: '',
    user: JSON.parse(localStorage.getItem('logInUserData')),
    mobile: JSON.parse(localStorage.getItem('logInUserData')).mobile,
    email: JSON.parse(localStorage.getItem('logInUserData')).email
  }

  postContactUsForm = (e) => {
    e.preventDefault()
    this.setState({ saveButtonText: 'Saving ...' })
    let { firstname, middlename, lastname, mobile } = this.state
    let user = JSON.parse(localStorage.getItem('logInUserData'))
    user.salutation = this.state.salutation
    user.name = firstname + ' ' + middlename + ' ' + lastname
    user.mobile = mobile
    localStorage.setItem('logInUserData', JSON.stringify(user))
    axios
      .post('/backendapi/adddetails', user)
      .then((res) => {
        window.location.reload()
      })
      .catch()
  }

  render() {
    return (
      <>
        {/* <Row sm="12" className="">
          <Media className="mb-2">
            <Media className="mr-2 my-25" left href="#">
              <Media
                className="users-avatar-shadow rounded"
                object
                src={
                  JSON.parse(localStorage.getItem('logInUserData')) &&
                  JSON.parse(localStorage.getItem('logInUserData'))
                    .salutation === 'Mr'
                    ? avatar2
                    : avatar1
                }
                alt="user profile image"
                height="84"
                width="84"
              />
            </Media>
            <Media className="mt-2" body>
              <Media className="font-medium-1 text-bold-600" tag="p" heading>
                {this.state.user.name}
              </Media>
            </Media>
          </Media>
        </Row> */}

        <Row xs="1">
          <Label for="optionSelect">Select a topic *:</Label>
          <Select
            id="optionSelect"
            className="React"
            classNamePrefix="select"
            ref={this.state.selectAssetRef}
            name="color"
            options={contactUsOptions}
            isClearable={true}
            placeholder="Select a topic..."
            onChange={(e) => {
              this.setState(
                {
                  contactUsFormType: e ? e.value : ''
                },
                this.saveContactUsFormValidation
              )
            }}
          />
        </Row>
        <Row>
          <Label for="description" className="mt-1">
            Description *
          </Label>
          <Input
            type="textarea"
            placeholder="Description"
            rows="5"
            id="description"
            onChange={(e) => {
              this.setState(
                {
                  contactUsDescription: e.target.value
                },
                this.saveContactUsFormValidation
              )
            }}
          />
        </Row>
        <Row>
          <Label for="attachment" className="mt-1">
            Attachments
          </Label>
          <Input
            type="File"
            placeholder="Attachments"
            rows="5"
            id="attachment"
          />
        </Row>

        <Col className="d-flex justify-content-end flex-wrap mt-2" sm="12">
          <Button.Ripple
            color="warning"
            disabled={
              !(
                this.state.contactUsDescription.length &&
                this.state.contactUsFormType.length
              )
            }
            className="button-label"
            onClick={(e) => {
              this.postContactUsForm(e)
            }}
          >
            {this.state.saveButtonText}
          </Button.Ripple>
        </Col>
      </>
    )
  }
}
export default UserAccountTab
